.projects {
    padding: 0 25%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 30px;
}

.slidebox img {
    width: 330px;
    height: 330px;
    border-radius: 5%; 
    transition: transform 0.3s ease;
    box-shadow: 0 0 20px rgba(6, 6, 6, 0.5);
}

.slidebox img:hover {
    transform: scale(1.1);
}



.slidebox {
    display: flex;
    justify-content: center;
    align-items: center;
}

.slidebox button {
    color: #707070;
    height: 100px;
    width: 80px;
    background-color: rgb(255, 255, 255);
    border-style: none;
    font-size: 3em;
    font-weight: 100;
    transition: transform 0.3s ease, background-color 0.3s ease;
}

.slidebox button:hover {
    color: #0c0c0c;
    transform: scale(1.2);
    /* Scale up on hover */
}


.slidebox button:active {
    transform: scale(0.7);
    transition-duration: 0s;
}

.slidebox button:active + img {
    box-shadow: 0 0 30px rgba(36, 36, 36, 0.5);
}



.projects h1 {
    color: #404040;
    font-size: 1.5rem;
    line-height: 1.6;
    font-weight: 300;
}

#projects {
    margin-top: 80px;
    margin-left: 15%;
}


.projectname{
    font-weight: 300;
    font-size: 2.5em;
}

@keyframes slideIn {
    from {
        opacity: 0;
        transform: translateY(-50px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}



.description {
    width: auto;
    height: 100%;
    animation: slideIn;
    animation-duration: 2s;
}

.tools{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 5px;
    margin-top: 15px;
}

.tools h2{
    color: #404040;
    /* color: #404040; */
    font-size: 1.5rem;
    line-height: 1.6;
    font-weight: 500;
    white-space: nowrap;
    

}
.tools h3{
    color: #404040;
    font-size: 1.5rem;
    line-height: 1.6;
    font-weight: 500;
}

@media (max-width:750px) {
    .slidebox{
        padding: 0;
        margin: 0 ;
    
    }

    .slidebox img{
        width: 250px;
        height: 250px;
    }

    .slidebox button{
        width: 10%;
    }

    .projects{
        padding: 0%;
    }

    .description{
        padding: 0 20px;
    }
    .projects h1 {
        font-size: 1.2em;
    }

    .projectname{
        font-weight: 300;
        font-size: 1.7em;
    }
    .tools {
        padding: 0 20px;
    }
    .tools h2{
        display: none;
    }
    .tools h3{
        font-size: 1.2em;
    }
    

}